import React from 'react'

const GetUser = (props) => {
    return (
        <div>
            <h1>Github users</h1>
      <div className="container-fluid mt-5">
        <div className="row text-center">
          {props.user.map((value, index) => {
            return (
              <div className="col-10 col-md-4 mt-5 ">
                <div className="card p-2" key={index}>
                  <div className="d-flex align-items-center">
                    <div className="image">
                      <img
                        src={value.avatar_url}
                        className="rounded"
                        style={{ width: "155px" }}
                      />
                    </div>
                    <div className="ml-3 w-100">
                      <h4 className="mb-0 mt-0 textLeft">
                        {value.login}
                        <span className="textLeft"></span>
                      </h4>

                      <div className="p-2 mt-2 bg-primary d-flex justify-content-between rounded text-white ">
                        <div className="d-flex flex-column">
                          <span className="articles">articles</span>
                          <span className="mumber1"></span>58
                        </div>
                        <div className="d-flex flex-column">
                          <span className="followers">followers</span>
                          <span className="mumber2"></span>58
                        </div>
                        <div className="d-flex flex-column">
                          <span className="rating">rating</span>
                          <span className="mumber3"></span>58
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
        </div>
    )
}

export default GetUser
